.loader {
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .loader_text {
    font-size: 2rem;
    font-weight: bold;
  }
  
  
  
  
  .container {
      max-width: 1200px;
  }
  
  
  .movies {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
  }
  
  .movie {
    width: 48%;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    overflow: hidden;
    background: #fff;
    margin-bottom: 70px;
    padding: 1rem;
    border-radius: .5rem;
    box-shadow: 0 13px 27px -5px rgba(0, 0, 0, .3);
  }
  
  .movie img {
      max-width: 120px;
      margin-right: 1.5rem;
  }
  
  .movie_summary {
    /* white-space: nowrap; */
    /* display: -webkit-box; */
    /* -webkit-line-clamp: 3; */
    /* 라인수 */
    /* -webkit-box-orient: vertical; */
    /* overflow: hidden; */
    /* text-overflow: ellipsis; */
  }
  
  .movie_genres {
      display: inline-flex;
      margin-bottom: 1rem;
      overflow: hidden;
  }
  
  .movie_genres li {
      margin-right: .5rem;
      font-size: .8rem;
  }
  @media (max-width: 786px) {
      .container {
          width: 80%;
      }
      .movie {
          width: 100%;
          flex-direction: column;
          align-items: center;
      }
  
      .movie img {
          max-width: 200px;
          margin: 0 0 1.5rem;
  
      }
  }